@import "../../include-media.scss";

.softProjsContent{
    background-image: linear-gradient(0deg, #45A29E, #1F2833);
    font-family: Montserrat;
    height: 50%;
    padding-top: 50px;
    padding-bottom: 50px;
    color: #C5C6C7;

    @include media("<desktop") {
        padding-bottom: 1px;
    }

    .softProjsList{
        padding-left: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .pageBreak{
            display: block;
            margin-left: auto;
            margin-right: auto;
            width: 20%;
            padding-top: 50px;
            padding-bottom: 50px;

            @include media("<desktop") {
                padding-top: 30px;
                padding-bottom: 30px;
            }
        }

        .softProj{
            //border: 3px solid red;
            margin: 20px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-evenly;

            @include media("<desktop") {
                margin: 0px;
                flex-direction: column;
            }
            

            .softProjDetails{
                //border: 3px solid green;
                //width: 50%;
                //float: left;
                margin: 100px;
                text-align: center;

                @include media("<desktop") {
                    margin: 20px;
                    font-size: 100%;
                    flex-direction: column;
                }

                .projTitle{
                    //border: 3px solid black;
                    font-size: 200%;
                    font-weight: bolder;

                    @include media("<desktop") {
                        font-size: 100%;
                    }
                }
        
                .projLang{
                    font-size: 150%;
                    padding-top: 15px;
                    padding-bottom: 15px;

                    @include media("<desktop") {
                        font-size: 80%;
                    }
                }

                .projDate{
                    //border: 3px solid black;
                    font-size: 145%;

                    @include media("<desktop") {
                        font-size: 75%;
                    }
                }

                // .codeBtn{
                //     //margin-right: 24px;
                // }
                
                .codeLink{
                    margin-top: 30px;
                    width: 50px;
                    border-radius: 50%;
                    background: #C5C6C7;
                    padding: 20px 20px;
                    font-weight: 400;
                    color: #0B0C10;
                    outline: none;
                    cursor: pointer;
                    transition: all 0.2s ease-in-out;
                    text-decoration: none;
                
                    @include media("<desktop") {
                        padding: 12px 12px;
                        width: 25px;
                    }

                    &:hover{
                        transition: all 0.2s ease-in-out;
                        background: #66FCF1;
                        color: #010606
                    }
                }
            }
            
            .projPic{
                //width: 600px;
                height: auto;
                position: relative;
                float: right;
                margin: 100px;
                width: 45%;
                max-width: 700px;
                height: auto;
                border-radius: 30px;

                @include media("<desktop") {
                    margin: 10px;
                    border-radius: 10px;
                    width: auto;
                    height: auto;

                    max-width: 70%;
                    max-height: 250px;
                }

            }

            #bookstorePic{
                width: 35%;
                max-width: 500px;
            }
        }

        #last_proj{
            border-bottom: none;
        }

        .mobileFlip{
            @include media("<desktop") {
                flex-direction: column-reverse;
            }
        }
    }

}