@import "../../include-media.scss";

.sectionHeader{
    //border: 3px solid green;
    font-weight: bold;
    position: absolute;
    color: #C5C6C7;
    font-family: Montserrat;
    text-shadow: -1px -1px 0 #0B0C10, 1px -1px 0 #0B0C10, -1px 1px 0 #0B0C10, 1px 1px 0 #0B0C10;

    @include media(">=desktop") {
        right: 0px;
        padding-right: 30px;

    }

    @include media("<desktop") {
        font-size: 60%;
        text-align: right;
        right: 0; 
        padding-right: 10px;


    }
}


.aboutContent{
    background-image: url("../../Images/code3.jpg");
    background-size: cover;
    font-family: Montserrat;
    background-position: center center;
    background-repeat: no-repeat;
    box-shadow: inset 0 0 0 1000px rgba($color: #1F2833, $alpha: 0.85);
    height: calc(100vh - 400px);
    padding-top: 150px;
    padding-bottom: 150px;
    //background-color: #1F2833;
    //border: 3px solid green;
    color: #C5C6C7;
    display: flex;
    //justify-content: space-evenly;


    @include media("<desktop") {
        height: 50%;
        flex-direction: column;
        padding-top: 50px;
        padding-bottom: 10px;
    }

    .left_container{
        //border: 3px solid green;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex: 1;
        margin: 50px;

        text-align: center;
        justify-content: space-around;
        

        @include media("<desktop") {
            margin: 0;
            margin-top: 20px;
            font-size: 50%;
        }

        .profilePic{
            //border: 3px solid black;
            border-radius: 50%;
            width: 80%;
            max-width: 600px;

            @include media("<desktop") {
                flex-direction: column;
                width: 50%;
            }
        }

        .social_buttons{
            //border: 3px solid black;
            //margin-top: 20px;
            display: flex;
            align-items: center;
            justify-items: center;
            column-gap: 30px;

            .gitBtn{
                display: flex;
                align-items: center;
            }
            
            .gitLink{
                width: 50px;
                border-radius: 30%;
                background: #45A29E;
                padding: 20px 20px;
                font-weight: 400;
                color: #0B0C10;
                outline: none;
                cursor: pointer;
                transition: all 0.2s ease-in-out;
                text-decoration: none;
            
                @include media("<desktop") {
                    width: 25px;
                    padding: 10px 10px;
                }

                &:hover{
                    transition: all 0.2s ease-in-out;
                    background: #C5C6C7;
                    color: #010606
                }
            }
    
            .linkdBtn{
                display: flex;
                align-items: center;
            }
            
            .linkdLink{
                width: 35px;
                border-radius: 30%;
                background: #45A29E;
                padding: 28px 27.5px;
                font-weight: 400;
                color: #0B0C10;
                outline: none;
                cursor: pointer;
                transition: all 0.2s ease-in-out;
                text-decoration: none;
            
                @include media("<desktop") {
                    width: 17.5px;
                    padding: 14px 13.25px;
                }

                &:hover{
                    transition: all 0.2s ease-in-out;
                    background: #C5C6C7;
                    color: #010606
                }
            }

        }

    }

    .right_container{
        //border: 3px solid red;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex: 1;
        margin: 50px;
        margin-right: 10%;

        @include media("<desktop") {
            margin: 30px;
            margin-top: 0;
        }

        .description{
            //border: 3px solid red;
            margin-top: 20px;
            font-size: larger;
            text-align: justify;
            text-justify: inter-word;
            padding-bottom: 100px;
            font-weight: 500;

            @include media("<desktop") {
                padding-top: 10px;
                font-size: 65%;
                padding-bottom: 30px;
            }
        }

        .contact_info{
            margin-top: 20px;
            font-size: larger;

            @include media("<desktop") {
                font-size: 65%;
            }
        }

    }
}