@import "../../include-media.scss";

.educationContent{
    //background-image: url("../../Images/lsu_tiger.jpg");
    background-image: linear-gradient(0deg, #0B0C10, #45A29E);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    height: 50%;
    //background-color: black;
    color: #C5C6C7;
    padding-top: 50px;
    font-family: Montserrat;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    //box-shadow: inset 0 0 0 2000px rgba($color: #C5C6C7, $alpha: 0.7);
    align-items: center;

    @include media("<desktop") {
        padding-bottom: 10px;
    }

    .degreeList{
        //border: 3px solid blue;
        display: flex;
        text-decoration: none;
        list-style: none;
        padding: 0;
        align-items: stretch;
        justify-content: space-between;

        @include media("<desktop") {
            flex-direction: column;
            font-size: 45%;
        }

        .degree{
            background-color: rgba($color: #1F2833, $alpha: 0.8);
            margin: 30px;
            border-radius: 30px;
            text-align: center;
            //border: 3px solid red;
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding-top: 30px;

            @include media("<desktop") {
                margin-bottom: 1px;
            }

            .degree_pic{
                border: 3px solid #0B0C10;
                width: 95%;
                height: auto;
            }

            #WIP{
                border: none;
            }
        }
    }

    .course_list{
        padding: 30px;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 50px;
        margin-left: 50px;
        margin-right: 50px;
        background-color: rgba($color: #1F2833, $alpha: 0.8);
        color: #C5C6C7;
        border-radius: 30px;
        margin-top: 25px;
        margin-bottom: 50px;

        @include media("<desktop") {
            margin-top: 10px;
            margin-left: 20px;
            margin-right: 20px;
            margin-bottom: 10px;
            gap: 20px;
            font-size: 10px;
        }


        .course{
            font-weight: 600;
            flex: 1 1 160px;
            //border: 3px solid red;
            text-align: center;
        }

    }

    .researchPapers{
        //margin-top: 85px;
        //border: 3px solid green;
        display: flex;
        text-decoration: none;
        list-style: none;
        padding: 0;
        align-items: stretch;
        justify-content: space-between;

        @include media("<desktop") {
            margin-top: -10px;
            flex-direction: column;
            font-size: 60%;
        }

        .paper{
            //border: 3px solid blue;
            background-color: rgba($color: #1F2833, $alpha: 0.8);
            border-radius: 30px;
            text-align: center;
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 20px;
            margin: 15px;

            @include media("<desktop") {
                padding: auto;
            }

            .paperImage{
                margin-bottom: 5%;

                &:hover{

                    opacity: 0.7;

                }

            }

            #paperImage0{
                width: 85%;
                border: 3px solid #0B0C10;
            }
            #paperImage1{
                width: 80%;
                border: 3px solid #0B0C10;
            }
            #paperImage2{
                padding-top: 13%;
                padding-bottom: 13%;;
                width: 100%;
            }

            .paperTitle{
                font-weight: 600;
                //text-shadow: -1px -1px 0 #0B0C10, 1px -1px 0 #0B0C10, -1px 1px 0 #0B0C10, 1px 1px 0 #0B0C10;
            }
        }
    }   

    .subheaders{
    //border: 3px solid green;
    height: 10px;
    font-weight: lighter;
    color: #C5C6C7;
    
    font-size: xx-large;
   }

    #school{
        //border: 3px solid green;
        font-size: 70px;
        font-weight: bolder;
        text-align: center;
        font-family: 'Times New Roman', Times, serif;
        color: #461D7C;
        text-shadow: -1px -1px 0 #FDD023, 1px -1px 0 #FDD023, -1px 1px 0 #FDD023, 1px 1px 0 #FDD023;

        @include media("<desktop") {
            font-size: 200%;
        }

    }

    #location{
        //border: 3px solid green;
        font-size: 35px;
        font-weight: bolder;
        text-align: center;
        color: #461D7C;
        text-shadow: -1px -1px 0 #FDD023, 1px -1px 0 #FDD023, -1px 1px 0 #FDD023, 1px 1px 0 #FDD023;

        @include media("<desktop") {
            padding-top: 5%;
            font-size: 100%;
        }

    }
}