@import "../../include-media.scss";

.employmentContent{
    //border: 3px solid red;
    background-image: linear-gradient(0deg, #1F2833, #0B0C10);
    height: 50%;
    font-family: Montserrat;
    padding-top: 70px;
    padding-bottom: 50px;    

    .jobsList{

        color: #1F2833;
        display: grid;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: 50px;
        padding: 0;

        .job{
            //border: 3px solid red;
            //border-bottom: 3px dotted #66FCF1;
            background-color: rgba($color: #45A29E, $alpha: 0.8);
            border-radius: 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-right: 100px;
            margin-left: 100px;
            flex: 1;

            @include media("<desktop") {
                margin-right: 20px;
                margin-left: 20px;
                flex-direction: column;
                font-size: 80%;
            }


            .jobDetails{
                height: auto;
                margin: 20px;
                padding-left: 40px;
                padding-right: 40px;
                flex: 1;

                @include media(">=desktop") {
                    width: 50%;
                }


                @include media("<desktop") {
                    margin: 0px;
                    width: 90%;
                    margin-top: 20px;
                    text-align: center;
                    padding-left: 0px;
                    padding-right: 0px;

                }

                .jobTitle{
                    //border: 3px solid black;
                    font-size: 200%;
                    font-weight: bolder;

                    @include media("<desktop") {
                        font-size: 90%;
                    }
                }

                .jobSubTitle{
                    font-size: 125%;
                    list-style: none;
                    font-weight: 800;

                    @include media("<desktop") {
                        font-size: 70%;
                    }

                }

                .jobPointsList{
                    list-style: none;
                    padding: 0;
                    font-size: 100%;
                    font-weight: 600;

                    @include media("<desktop") {
                        font-size: 70%;
                    }

                    .jobPoint{
                        padding: 35px;
                        background-color: #C5C6C7;
                        margin-bottom: 30px;
                        border-radius: 30px;

                        @include media("<desktop") {
                            padding: 5%;
                            margin-bottom: 20px;
                        }
                    }
                }

                #geauxtrace_link{
                    color: #8d40f1;
                    align-items: center;
                    text-decoration: none;
                    list-style: none;
                    cursor: pointer;
                
                    &:hover{
                        color: white;
                
                        &:active{
                            color: #FDD023;
                        }
                    }
                }
            }
            
            .jobPic{
                //border: 3px solid blue;
                width: 50%;
                height: auto;
                max-width: 750px;
                max-height: 700px;
                margin: 30px;
                border-radius: 30px;

                @include media("<desktop") {
                    font-size: 80%;
                    max-width: 90%;
                    max-height: 300px;
                    margin-top: 0%;
                    margin-bottom: 7%;
                    border-radius: 10px;
                }
            }

            #geauxtracePic{
                width: 35%;
                max-height: none;

            }
        }

        .right{
            text-align: right;
        }

        .mobileFlip{
            @include media("<desktop") {
                flex-direction: column-reverse;
            }
        }
    }

}