@import "../../include-media.scss";

// Navigation Bar Styles
.Nav {
    background: #0B0C10;
    height: 80px;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem calc((100vw - 1000px) / 2);
    z-index: 10;
    //position: sticky;
    top: 0;
    font-family: Montserrat;

    @include media(">=desktop") {
        position: sticky;    
    }

    @include media("<desktop") {
        height: 100vh;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;        
    }
}

.NavLink {
   
    background: #0B0C10;
    color: #C5C6C7;
    display: flex;
    font-weight: 500;
    align-items: center;
    text-decoration: none;
    list-style: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;

    &:hover{
        color: white;

        &:active{
            color: #66FCF1;
        }
    }

    @include media("<desktop") {
        height: 0%;
    }
}

