.NavMenu {
    display: flex;
    align-items: center;
    margin-right: -24px;

    @include media("<desktop") {
    //background-color: orange;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        margin-right: 0px;
        height: 40%;
        padding-top: 10px;
    }
}

.NavBtn{
    display: flex;
    align-items: center;
    margin-right: 24px;

    @include media("<desktop") {
        margin-right: 0;
        //height: 20%;
    }
}

.NavBtnLink{
    border-radius: 4px;
    background: #45A29E;
    padding: 10px 20px;
    font-weight: bolder;
    color: #0B0C10;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;

    &:hover{
        transition: all 0.2s ease-in-out;
        background: #C5C6C7;
        color: #010606
    }
}

a{text-decoration: none;}