@import "../../include-media.scss";

.skillsContent{
    //background-image: url("../../Images/code2.jpg");
    background-image: linear-gradient(0deg, #45A29E, #1F2833);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    height: 50%;
    padding-top: 50px;
    padding-bottom: 50px;
    display: flex;
    font-family: Montserrat;
    //gap: 30px;
    //align-content: space-between;
    justify-content: space-around;
    //background-color: #d3f5c3;

    @include media(">=desktop") {
        flex-wrap: wrap;        
    }

    @include media("<desktop") {
        flex-direction: column;
        padding-top: 30px;
        padding-bottom: 20px;          
    }

    .skillsCategory{
        //border: 3px solid red;
        flex: 0 0 30%;
        margin: 30px;
        height: auto;
        text-align: center;
        display: flex;
        flex-direction: column;
        //justify-content: space-evenly;
        background-color: #0B0C10;
        color: #C5C6C7;
        border-radius: 30px;

        @include media("<desktop") {
            margin: 20px;
            margin-bottom: 0px;    
        }

        .skillsHeader{
            font-size: 150%; 

            @include media("<desktop") {
                font-size: 75%;       
            }
        }

        .skillsList{
            //border: 3px solid green;
            height: auto;
            padding: 0;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            align-content: center;
            justify-content: center;

            @include media("<desktop") {
                height: auto;       
            }

            .skill{
                margin: 10px;
                text-align: center;
                font-size: 100%;
                font-weight: 500;

                @include media("<desktop") {
                    font-size: 65%;            
                }
            }
        }
    }
}